.custom-checkbox-label {
  color: #222; /* Darker label text */
  font-weight: 600; /* Make label bolder */
  font-size: 1rem; /* Increase font size */
  display: flex;
  align-items: center; /* vertically centers checkbox and label text */
  gap: 8px; /* optional spacing between checkbox and label */
}

.custom-checkbox input[type='checkbox'] {
  width: 22px;
  height: 22px;
  border: 3px solid #214162; /* bold border */
  accent-color: #214162; /* check color */
  cursor: pointer;
}

.custom-checkbox-input {
  width: 22px !important;
  height: 22px !important;
  border: 3px solid #214162 !important;
  accent-color: #214162 !important;
  cursor: pointer;
  border-radius: 4px;
}
